<template>
  <div id="app">
    <NavBar :user="user" />
    <div id="content" class="container" style="margin-top:30px">
      <div class="row">
        <div class="col-sm-8">
          <Content :page="page" />
        </div>
        <div class="col-sm-4">
          <SideBar />
        </div>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import NavBar from "./components/NavBar.vue";
import Content from "./components/Content.vue";
import SideBar from "./components/SideBar.vue";
import Footer from "./components/Footer.vue";

export default {
  name: "app",
  data() {
    var data = {
      page: "",
      user: {
        islogin: false,
        username: "Loading...",
        email: "Loading...",
        qq: 0,
        iconUrl: "Loading..."
      }
    };
    var arrUrl = window.location.href.split("/");
    data.page = arrUrl[arrUrl.length - 1];

    document.title = "CCBOT - " + data.page;
    return data;
  },
  components: {
    NavBar,
    Content,
    SideBar,
    Footer
  },
  created() {
    fetch(this.$config.api_base + "auth/fastapi", {
      method: "GET",
      mode: "no-cors",
      credentials: "include"
    })
      .then(data => data.json())
      .then(data => {
        if (data.code == 200) {
          this.username = data.username;
          this.email = data.email;
          this.qq = data.qq;
          this.iconUrl =
            "https://q2.qlogo.cn/headimg_dl?dst_uin=" +
            this.qq +
            "&spec=100&url_enc=0&referer=bu_interface&term_type=PC";
          this.islogin = true;
        }
      });
  }
};
</script>

<style>
@import "./assets/docs.css";
</style>
